.box.highlight {
  text-align: center;
}

.box.map {
  border: solid 0.15em #9998A3;
}

.box img {
  max-width: 5em;
  max-height: 5em;
  position: absolute;
  top: 50%;
  left: 50%;
  margin: -2.5em 0 0 -2.5em;
}

.image {
  border: 0;
  display: inline-block;
  position: relative;
  border-radius: 5px;
}

.image img {
  display: block;
  border-radius: 5px;
}

.image.left {
  display: block;
  float: left;
  margin: 0 2em 2em 0;
  position: relative;
  top: 0.25em;
}

.image.left img {
  display: block;
  width: 100%;
}

.icon {
  position: relative;
  background-color: #37c0fb;
  border-radius: 100%;
  box-shadow: 0 0 0 7px white, 0 0 0 8px #e0e0e0;
  width: 8em;
  height: 8em;
  margin: 0 auto 2em auto;
}

#footer {
  padding: 4em 0 8em 0;
}

#footer .container {
  margin-bottom: 4em;
}

#footer .copyright {
  color: #999;
  margin-top: 1.5em;
  text-align: center;
  font-size: 0.9em;
}

#header {
  text-align: center;
  padding: 3em 0 0 0;
  background-color: #fff;
}

#header h1 {
  padding: 0 0 2.75em 0;
  margin: 0;
}

#header h1 a {
  font-size: 1.5em;
  letter-spacing: -0.025em;
  border: 0;
}

header p {
  color: #999;
  font-size: 1.25em;
  position: relative;
  margin-top: -1.25em;
  margin-bottom: 2.25em;
}

header.major {
  text-align: center;
  margin: 0 0 2em 0;
}

header.major h2 {
  font-size: 2.25em;
  border-bottom: solid 1px #e0e0e0;
  padding: 0 0 0.25em 0;
  margin: 0 0 1em 0;
}

header.major p {
  position: relative;
  border-top: solid 1px #e0e0e0;
  padding: 1em 0 0 0;
  margin: 0;
  top: -1em;
  font-size: 1.5em;
  letter-spacing: -0.025em;
}

header.major img {
  max-width: 8em;
  max-height: 8em;
}

.wrapper {
  padding: 5em 0 3em 0;
}

.wrapper.style1 {
  background-color: #fff;
}

.wrapper.style2 {
  background-color: #37c0fb;
  color: #fff;
}

.wrapper.style2 .button {
  background: #fff;
  color: #474747;
}

.wrapper.style2 .button.hover {
  color: #37c0fb;
}

#banner {
  background-image: url(/automation-demo/static/media/stock-photo-engineer.d8cdeff9.jpg);
  background-position: center center;
  background-size: cover;
  height: 28em;
  text-align: center;
  position: relative;
}

#banner header {
  position: absolute;
  bottom: 0;
  left: 0;
  width: 100%;
  background: #212121;
  background: rgba(27, 27, 27, 0.75);
  color: #fff;
  padding: 1.5em 0;
}

#banner header h2 {
  display: inline-block;
  margin: 0;
  font-size: 1.25em;
  vertical-align: middle;
}

#banner header h2 em {
  opacity: 0.75;
}

#banner header .button {
  vertical-align: middle;
  margin-left: 1em;
}

.container {
  margin: 0 auto;
  max-width: 100%;
  width: 1400px;
}

